body{
  font: 15px/1 "Hiragino Kaku Gothic Pro", "ヒラギノ角ゴ Pro W3", "メイリオ", "Meiryo", Helvetica, Arial, "ＭＳ Ｐゴシック", sans-serif;
  padding: 50px 0;
  color: #646262;
}

sub {
    bottom: -.19em;
}

.App{
  display: flex;
  align-items: center;
  justify-content: center;
}

.page{
  width: 1209px;
  height: 1720px;
  transform-origin: top left;
  top: 50%;
  left: 50%;
}
.page_content{
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.page div, .page img{
  position: absolute;
}

.control_panel{
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}

.controls {
  padding: 7px 10px;
  left: 0;
  right: 0;
  bottom: 0;
  position: absolute;
  background: #444444;
  display: flex;
  align-items: center;
  justify-content: center;
}
.controls > * {
  margin: 0 10px;
}

.controls_button{
  color: #ffffff;
  border: 0 none;
  outline: 0 none;
  border-radius: 4px;
  background: transparent;
  padding: 7px 10px;
  cursor: pointer;
}

.controls_button:disabled{
  cursor: not-allowed;
  color: #ababab;
}

.controls_button:not(:disabled):hover{
  background: rgba(255,255,255,0.3);
}
.controls_button:not(:disabled):active{
  background: transparent;
}

.progress_bar{
  background: #444444;
  border: 1px solid #ffffff;
  height: 12px;
  width: 200px;
}
.progress_fill{
  background: #ffffff;
  height: 10px;
}

.side_bar{
  position: fixed;
  background: #444444;
  left: 0;
  right: 0;
  top: 0;
  padding: 7px 10px;
  z-index: 1;
}

.side_bar .controls_button{
  display: none;
}

.search_result{
  max-height: 30vh;
  overflow: auto;
}

.result_item{
  margin-top: 10px;
  padding: 10px;
  border-radius: 4px;
  color: #ffffff;
  cursor: pointer;
}

.result_item:hover{
  background: rgba(255,255,255,0.3);
}

.page_preview{
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

@media screen and (min-width: 768px){
  body{
    padding: 0;
  }
  .control_panel{
    opacity: 0;
    padding: 20px 0;
    transition-property: opacity;
    transition-duration: 0.5s;
    transition-delay: 0.5s;
  }
  .control_panel.active{
    opacity: 1;
  }

  .control_panel:hover{
    opacity: 1;
    transition-delay: 0s;
  }

  .controls{
    position: relative;
    border-radius: 4px;
    left: auto;
    right: auto;
    bottom: auto;
  }

  .side_bar{
    width: 300px;
    left: -300px;
    right: auto;
    bottom: 0;
    transition-property: left;
    transition-duration: 0.3s;
  }
  .side_bar.active{
    left: 0;
  }

  .side_bar .controls_button{
    display: block;
    position: absolute;
    right: -50px;
    top: 10px;
    background: #444444;
    cursor: pointer;
  }

  .side_bar .controls_button:hover{
    background: rgba(68,68,68,0.5);
  }
  .side_bar .controls_button:active{
    background: transparent;
  }

  .search_result{
    max-height: none;
  }
}